        <el-form-item label="商城订单号" size="mini">
          <span>{{formInline.shopping_order_sn||''}}</span>
        </el-form-item>
        <el-divider></el-divider>
        <el-form-item label="往来类型：" size="mini">
          {{formInline.accounts_type_name}}
        </el-form-item>
        <el-form-item label="业务日期：" size="mini">
          {{formInline.business_time}}
        </el-form-item>
        <el-form-item label="优惠金额" size="mini" v-if="is_company_service">
          {{formInline.discount_price}}
        </el-form-item>
        <el-form-item label="部门：" size="mini">
          {{formInline.department_name}}
        </el-form-item>
        <el-form-item label="业务员：" size="mini">
