        </el-form-item>
      </el-form>
    </div>
    <div class="paymanageTitle">
      <i></i>
      <span>结算明细</span>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.receipt_common" style="width: 100%" :summary-method="getSummaries" show-summary max-height="600">
        <el-table-column :show-overflow-tooltip="true" type="index" width="46" align="center" label="序号"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="receivables_mode_name" label="收款方式">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="bank_name" label="开户银行">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="bank_number" label="银行账号">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="common_price" label="金额">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注">
        </el-table-column>
        <el-table-column label="图片附件" property="fileList" :show-overflow-tooltip="true" min-width="400">
          <template slot-scope="scope">
            <div>
              <ul>
                <li v-for="(val,index) in scope.row.fileList" :key="index" style="height:36px;float:left;margin-right:3px;width: 44px;cursor:pointer">
                  <img :src="val.url" :alt="val.name" style="width:100%;height:100%;" @click="opent(val.url)">
                </li>
              </ul>
            </div>
          </template>
        </el-table-column>
