            <vxe-table-column field="sc_price" title="实际应收金额" width="130">
              <template v-slot:header>
                <vxe-tooltip v-model="showSexTip" content="实际应收金额=本出库单中出库金额">
                  <span>实际应收金额<i class="el-icon-question" @click="showSexTip = !showSexTip"></i></span>
                </vxe-tooltip>
              </template>
            </vxe-table-column>
            <vxe-table-column field="business_time" width="120" title="业务日期"></vxe-table-column>
            <vxe-table-column field="add_time" width="120" title="单据时间"></vxe-table-column>
            <vxe-table-column field="remark" width="120" title="备注"></vxe-table-column>
            <vxe-table-column field="rc_price" width="120" title="已冲销金额"></vxe-table-column>
            <vxe-table-column field="bill_receipt_discount_price" width="120" title="已优惠金额" :visible="Boolean(is_company_service)"></vxe-table-column>
            <vxe-table-column field="rest_price" width="120" title="未冲销金额"></vxe-table-column>
            <vxe-table-column field="bill_discount_price" width="120" title="本次优惠金额" :visible="Boolean(is_company_service)"></vxe-table-column>
            <vxe-table-column field="bill_price" width="120" title="本次冲销金额"></vxe-table-column>
          </vxe-table>
        </div>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <!--未审核-->
      <template v-if="formInline.auditor_status==1">
        <el-button type="primary" @click="examineFun" v-if="authority.b_pass==1">审 核</el-button>
        <el-button @click="abolishFun" v-if="authority.b_invalid==1">作 废</el-button>
      </template>
      <!--作废-->
      <template v-if="formInline.auditor_status==3 || (authority.b_invalid!=1 && authority.b_pass!=1)">
        <el-button @click="goBack">返 回</el-button>
      </template>
      <template v-else>
        <el-button @click="goBack">取 消</el-button>
      </template>
