<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "customeReceiptmanageSearch",
  data() {
    return {
      goBackText: null,
      authority: {},
      formInline: {},
      dialogImageUrl: "",
      TableData: {},
      dialogVisibleimg: false,
      //客户理赔
      isShow_sbList: false,
      sbListTabel: {
        tableData: [],
      },
      //    客户返利
      isShow_flList: false,
      flListTabel: {
        tableData: [],
      },

      //    销售出库
      isShow_xschukuList: false,
      xsListTabel: {
        tableData: [],
      },

      //判断是否从审核页查看
      receiptaudit: "",
      receipt_id: "",

      is_disabled: false,
      relationTable: {
        tableData: [],
      },
      title: "",
      changeIndex: 0,
      hexiaoDetail: true,
    };
  },
  // computed: {
  //   ...mapGetters(["receiptmanageSearch_showGet"]),
  // },
  methods: {
    // ...mapActions(["receiptmanageIndexACT", "receiptauditDefault_show"]),
    goBack() {
      this.$router.push({
        path: "/custom/receiptmanage",
      });
      // if (this.receiptaudit == "审核") {
      //   this.receiptauditDefault_show({
      //     // commont: "receiptauditDefault",
      //     params: this.receiptmanageSearch_showGet.params,
      //     goPage: this.receiptmanageSearch_showGet.goPage,
      //     pageSize: this.receiptmanageSearch_showGet.pageSize,
      //   });
      // } else {
      //   this.receiptmanageIndexACT({
      //     // commont: "Index",
      //     params: this.receiptmanageSearch_showGet.params,
      //     goPage: this.receiptmanageSearch_showGet.goPage,
      //     pageSize: this.receiptmanageSearch_showGet.pageSize,
      //   });
      // }
    },
    footerMethod({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "合计";
          }
          if (
            [
              "sc_price",
              "rc_price",
              "rest_price",
              "bill_price",
              "bill_discount_price",
            ].includes(column.property)
          ) {
            this.cxhj = this.$utils.sum(data, "bill_price").toFixed(2);
            this.bcyh = this.$utils.sum(data, "bill_discount_price").toFixed(2);
            return this.$utils.sum(data, column.property).toFixed(2);
          }
          return null;
        }),
      ];
    },
    opent(val) {
      this.dialogImageUrl = val;
      this.dialogVisibleimg = true;
    },
    renderHeader() {
      let str = "";
      switch (this.formInline.accounts_type_name) {
        case "销售收款": //销售收款
        case "物流代收": //物流代收
          str = "实际应收金额=本出库单中出库金额";
          this.hexiaoDetail = true;
          break;
        case "预收款": //预收款
        case "其他收款": //其他收款
          this.hexiaoDetail = false;
          break;
        default:
          break;
      }
      return (
        <el-tooltip class="item" effect="dark" placement="top">
          <p slot="content">{str}</p>
          <span
            class="el-icon-question"
            style="cursor:pointer;font-weight:bold"
          >
            实际应收金额
          </span>
        </el-tooltip>
      );
    },
    changeTitle(index) {
      this.changeIndex = index;
    },
    examineFun() {
      //审核
      this.$axios({
        method: "post",
        url: this.$api.auditorRecePass,
        data: {
          receipt_id: this.receipt_id,
        },
      }).then((res) => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.msg || res.data.data,
            type: "success",
          });
          this.goBack();
        }
      });
    },
    abolishFun() {
      //作废
      this.$axios({
        method: "post",
        url: this.$api.auditorReceInvalid,
        data: {
          receipt_id: this.receipt_id,
        },
      }).then((res) => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.msg || res.data.data,
            type: "success",
          });
          this.goBack();
        }
      });
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "总计";
          return;
        }
        const values = data.map((item) => Number(item[column.property]));
        if (!values.every((value) => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index == 4) {
            sums[index] = parseFloat(sums[index]).toFixed(2);
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    },
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    var receiptId = this.$route.query.receipt_id;
    this.receiptaudit = this.$route.query.id || "";
    let vm = this;
    this.$axios({
      method: "get",
      url: this.$api.getReceiptInfo,
      params: {
        receipt_id: receiptId,
        receipt_sn: this.$route.query.receipt_sn,
        is_edit: 0,
      },
    }).then(({ data }) => {
      if (data.code == 200) {
        this.$set(this, "TableData", data.data);
        this.formInline = this.TableData;
        this.$axMethod
          .getCustomerInfoNEWqx({
            customer_id: vm.formInline.customer_id,
            company_id: vm.formInline.company_id,
          })
          .then(({ data }) => {
            if (data.code == 200) {
              [
                "deposits",
                "balance",
                "credit_balance",
                "credit_quota",
                "contact_name",
                "customer_mobile",
                "business_id",
              ].forEach((item) => {
                this.$set(vm.formInline, item, data.data[item]);
              });
            }
          });
        this.receipt_id = receiptId;
        this.formInline.auditor_status_name == "未审核"
          ? (this.goBackText = "取 消")
          : (this.goBackText = "返 回");
        if (data.data.receipt_common_bill.length) {
          this.is_disabled = true;
          setTimeout(() => {
            if (this.$refs.xTable) {
              this.$refs.xTable.reloadData(data.data.receipt_common_bill);
              this.$refs.xTable.updateFooter();
            }
          }, 500);
        } else {
          this.is_disabled = false;
        }
      }
    });
    //检测url中是否含有内部记账依据明细表传来的数据，如果有去除掉，方便返回
    // if (this.$route != "custom/receiptmanage") {
    //   this.$router.replace({ path: "/custom/receiptmanage" });
    // }
  },
};
</script>

